<template>
  <div fill-height class="d-flex flex-column align-center" style="width:100%">
    <v-app-bar fixed class="top" color="background" app flat>
      <v-btn fab text small @click="$router.back(-1)" class="mt-3">
        <v-icon large>mdi-arrow-left</v-icon>
      </v-btn>
      <span class="text_middle mt-3">发帖</span>
    </v-app-bar>
    <div class="main_container_edit">
      <v-divider></v-divider>
      <span class="d-flex py-2 align-center">
        <v-avatar size="36px">
          <v-icon color="primary">mdi-earth</v-icon>
        </v-avatar>
        <span>分类</span>
        <v-spacer></v-spacer>

        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn text large v-bind="attrs" v-on="on">
              <span class="text_small">{{info.category==''?'选择分类':info.category}}</span>
              <v-icon class="mr-n3">mdi-dots-vertical</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-item
              v-for="(item, index) in items_category"
              :key="index"
              @click="info.category=item"
            >
              <v-list-item-title>{{ item }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </span>
      <v-divider></v-divider>
      <span class="d-flex py-2 align-center">
        <v-avatar size="36px">
          <v-icon color="primary">mdi-tag-multiple</v-icon>
        </v-avatar>
        <span>标签</span>
        <v-spacer></v-spacer>

        <v-menu offset-y>
          <template v-slot:activator="{ on, attrs }">
            <v-btn large text small v-bind="attrs" v-on="on" >
              <span class="text_small">{{info.tag==''?'选择标签':info.tag}}</span>
              <v-icon class="mr-n3">mdi-dots-vertical</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-item v-for="(item, index) in items_tag" :key="index" @click="info.tag=item">
              <v-list-item-title>{{ item }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </span>
      <v-divider></v-divider>

      <span class="d-flex py-2 align-center">
        <v-avatar size="36px">
          <v-icon color="primary">mdi-account-box</v-icon>
        </v-avatar>
        <span>头像</span>
        <v-spacer></v-spacer>
        <span @click="chooseAvatar()" class="mr-n1">{{avatarsChoosed==10?'选择头像':'已选择'}}</span>
        <v-btn fab text small @click="chooseAvatar()">
          <v-icon size="20px">mdi-select-search</v-icon>
        </v-btn>
      </span>
      <v-divider></v-divider>

      <v-text-field
        label="输入标题"
        placeholder="输入标题,不超过50字"
        :rules="titleRule"
        counter="50"
        clearable
        outlined
        class="mt-5"
        v-model="info.title"
      ></v-text-field>
      <span class="d-flex mt-n5 align-center justify-space-around">
        <v-file-input accept="image/*" counter multiple label="可选择插入图片" @change="imageChange"></v-file-input>
        <v-btn color="primary" outlined width="30%" class="ml-3" @click="showVoteDialog()">
          <span class="text_xsmall" v-if="!voteDialog.confirm">可选添加投票</span>
          <span class="text_xsmall" v-if="voteDialog.confirm">已添加投票</span>
        </v-btn>
      </span>
      <v-textarea
        outlined
        name="input-7-4"
        label="输入内容"
        class="mt-2"
        placeholder="输入内容,不超过400字"
        :rules="contentRule"
        auto-grow
        counter="400"
        v-model="info.content"
      ></v-textarea>

      <v-btn class="float-right" color="primary" @click="submit" style="z-index:99">
        <v-icon left>mdi-send</v-icon>
        <span>提交</span>
      </v-btn>
    </div>
    <v-img style="position:absolute" class="image" :src="require('../../static/images/纸飞机2.png')"></v-img>

    <!-- 加载环 -->
    <v-overlay absolute :value="overlay">
      <v-progress-circular indeterminate color="primary"></v-progress-circular>
      <span>{{loading_info}}</span>
    </v-overlay>

    <!-- 弹窗 -->
    <v-snackbar v-model="snackbar.show" top :color="snackbar.color" app>
      {{ snackbar.message }}
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar.show = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </template>
    </v-snackbar>

    <!-- 选择头像对话框 -->
    <v-dialog
      v-model="avatarChooseDialog"
      scrollable
      :overlay="false"
      transition="dialog-transition"
      max-width="300px"
      max-height="500px"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title>
          <span class="text_small">{{avatarsChoosed==10?'选择头像':`已选择第${avatarsChoosed+1}个头像`}}</span>
          <v-spacer></v-spacer>
          <v-btn @click="avatarChooseDialog=!avatarChooseDialog" fab text small>
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="d-flex flex-column">
          <span class="grey--text text_small-- ml-3 mt-2">选择一个你心仪的头像,用于本帖</span>
          <span class="grey--text text_small-- ml-3">
            选定之后在本帖内你
            <span class="white--text text_thick">均使用该头像</span>
          </span>
          <span class="grey--text text_small-- ml-3">点击头像图片即可选择</span>
          <div class="d-flex justify-space-around flex-wrap mt-2">
            <v-avatar
              v-for="(item,i) in avatars"
              :key="i"
              size="60"
              class="flex_3 mb-2"
              @click="avatarsChoosed=i;info.avatar=item[0].url;"
            >
              <img :src="item[0].url" alt="头像" :class="avatarsChoosed==i?'lucency':''" />
              <v-icon
                :class="avatarsChoosed==i?'':'d-none'"
                style="position:absolute"
                color="white"
                size="36"
              >mdi-check-circle</v-icon>
            </v-avatar>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="voteDialog.show"
      scrollable
      :overlay="false"
      max-width="500px"
      persistent
      transition="dialog-transition"
      dark
    >
      <v-card style="background-color:#363636">
        <v-card-title class="d-flex align-center">
          <v-icon color="primary">mdi-vote</v-icon>
          <span class="text_small ml-1">添加投票</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text>
          <v-text-field label="输入投票问题" v-model="voteDialog.question" class="mt-1"></v-text-field>
          <span>选项数目</span>
          <v-slider
            v-model="voteDialog.slider"
            class="align-center mb-8"
            :tick-labels="['2','3','4','5','6']"
            :max="voteDialog.max"
            :min="voteDialog.min"
            hide-details
          >
            <template v-slot:append>
              <v-avatar size="20" color="red">
                <span style="color:white">{{voteDialog.slider}}</span>
              </v-avatar>
            </template>
          </v-slider>
          <v-text-field
            v-for="(i,item) in voteDialog.slider"
            :key="i"
            name="name"
            :label="'第'+i+'个选项描述'"
            v-model="voteDialog.answer[i-1]"
            id="id"
            dense
          ></v-text-field>
          <div class="float-right">
            <v-btn color="primary" small @click="exitVoteDialog" outlined>直接退出</v-btn>
            <v-btn
              color="primary"
              small
              v-if="voteDialog.confirm"
              @click="voteDialog=deleteVoteDialog"
              outlined
            >删除投票</v-btn>
            <v-btn color="primary" small @click="confirmVote">添加投票</v-btn>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import isNull from '@/utils/isNull';
import Authorization from '@/utils/Authorization'
export default {
  name: "edit",
  data() {
    return {
      overlay: false,
      avatarChooseDialog: false,
      loading_info: "",
      snackbar: {
        show: false,
        message: "",
        color: "",
      },
      titleRule: [(v) => v.length <= 50 || "Max 50 characters"],
      contentRule: [(v) => v.length <= 400 || "Max 400 characters"],
      items_category: ["校园", "社会", "情感", "娱乐", "体育", "其他"],
      items_tag: ["无", "性相关", "引战", "令人不适", "政治相关", "未经证实"],
      items_name: ["四大名著"],
      info: {
        category: "",
        tag: "",
        name: "",
        pics: "",
        avatar: "",
        title: "",
        content: "",
      },
      avatars: [],
      avatarsChoosed: 10,
      files: [],
      voteDialog: {
        show: false,
        question: "",
        slider: 3,
        max: 6,
        min: 2,
        answer: Array(6),
        confirm: false,
      },
      deleteVoteDialog: {
        show: true,
        question: "",
        slider: 3,
        max: 6,
        min: 2,
        answer: Array(6),
        confirm: false,
      }
    };
  },
  created() {
    console.log(location.hash);
    let that = this;
    //cordova监听键盘
    window.addEventListener("native.keyboardshow", function (e) {
      that.$store.change_keyboard(true);
    });
  },
  methods: {
    imageChange(files) {
      this.files = files;
    },
    showVoteDialog() {
      this.voteDialog.show = true;
    },
    confirmVote() {
      if (this.voteDialog.question == "" || isNull(this.voteDialog.question)) {
        this.showSnackbar("投票问题不能为空");
        return;
      }
      for (let i = 0; i < this.voteDialog.slider; i++) {
        if (this.voteDialog.answer[i] == undefined || isNull(this.voteDialog.answer[i])) {
          this.showSnackbar("第" + (i + 1) + "个选项描述不能为空"); return;
        }
      }
      for (let i = 0; i < this.voteDialog.slider; i++) {
        if (this.voteDialog.answer[i].length > 20) {
          this.showSnackbar("第" + (i + 1) + "个选项描述不能超过20字"); return;
        }
      }
      this.voteDialog.confirm = true;
      this.voteDialog.show = false;
    },
    exitVoteDialog() {
      if (!this.voteDialog.confirm) this.voteDialog.show = false;
      else {
        if (this.voteDialog.question == "" || isNull(this.voteDialog.question)) {
          this.showSnackbar("投票问题不能为空");
          return;
        }
        for (let i = 0; i < this.voteDialog.slider; i++) {
          if (this.voteDialog.answer[i] == undefined || isNull(this.voteDialog.answer[i])) {
            this.showSnackbar("第" + (i + 1) + "个选项描述不能为空"); return;
          }
        }
        for (let i = 0; i < this.voteDialog.slider; i++) {
          if (this.voteDialog.answer[i].length > 20) {
            this.showSnackbar("第" + (i + 1) + "个选项描述不能超过20字"); return;
          }
        }
        this.voteDialog.show = false;
      }
    },
    judge() {
      let info = this.info;
      if (info.category == "") {
        this.snackbar = {
          show: true,
          message: "请选择分类",
          color: "grey darken-3",
        };

        return false;
      } else if (info.tag == "") {
        this.snackbar = {
          show: true,
          message: "请选择标签",
          color: "grey darken-3",
        };
        return false;
      } else if (info.avatar == "") {
        this.snackbar = {
          show: true,
          message: "请选择头像",
          color: "grey darken-3",
        };
        return false;
      } else if (info.title == ""||isNull(info.title)) {
        this.snackbar = {
          show: true,
          message: "标题不能为空",
          color: "grey darken-3",
        };
        return false;
      } else if (info.content == ""||isNull(info.content)) {
        this.snackbar = {
          show: true,
          message: "内容不能为空",
          color: "grey darken-3",
        };
        return false;
      }

      //判断文本长度
      if (info.title.length > 50) {
        this.snackbar = {
          show: true,
          message: "标题不能超过50个字",
          color: "grey darken-3",
        };
        return false;
      }
      if (info.content.length > 400) {
        this.snackbar = {
          show: true,
          message: "正文不能超过400个字",
          color: "grey darken-3",
        };
        return false;
      }
      return true;
    },
    submit() {
      let that = this;

      //判断文本合法性
      if (!this.judge()) return;
      //判断图片上传
      if (this.files.length > 1) {
        this.snackbar = {
          show: true,
          message: "最多只能上传一张照片",
          color: "grey darken-3",
        };
      }
      //有图片则上传图片
      if (this.files.length == 1) {
        console.log(this.files);
        console.log("点击");
        this.overlay = true;
        this.loading_info = "正在上传图片";
        let img = this.files[0];
        const data = new FormData();
        data.append("img", img);
        data.append("format", "json");
        this.$axios
          .post(that.$global.url + "/edit/imageUpload", data, {
            headers: {
              "Content-Type": "multipart/form-data",
              token: localStorage.getItem("token"),
              auth: Authorization.f()
            },
          })
          .then((res) => {
            res = res.data;
            if (res.status == "error") {
              that.overlay = false;
              this.showSnackbar(res);
            }
            //成功上传之后再继续上传文本
            else if (res.status == "ok") {
              that.loading_info = "正在提交";
              this.showSnackbar(res, 'ok')
              this.info.pics = res.data;
              that.$request('post', '/edit/formupload', {}, {
                category: that.info.category,
                tag: that.info.tag,
                pics: that.info.pics,
                title: that.info.title,
                content: that.info.content,
                avatar: that.avatars[this.avatarsChoosed][0].id,
                tips: !this.voteDialog.confirm
                  ? undefined
                  : [
                    {
                      type: "vote",
                      question: this.voteDialog.question,
                      answers: this.voteDialog.answer
                        .slice(0, this.voteDialog.slider)
                        .join("$^^$"),
                    },
                  ],
              })
                .then((res) => {
                  that.overlay = false;
                  that.showSnackbar(res, 'ok')
                  setTimeout(() => {
                    that.$router.go(-1)
                  }, 500);
                })
                .catch(e => {
                  that.overlay = false;
                  console.log(e)
                  this.showSnackbar(e)
                })
            } else {
              that.overlay = false;
              this.showSnackbar(res);
            }
          })
          .catch((err) => {
            console.log(err);
            that.showSnackbar(err)
          });
      }
      //没有图片就直接上传得了
      else if (this.files.length == 0) {

        that.$request('post', '/edit/formupload', {}, {
          category: this.info.category,
          tag: this.info.tag,
          title: this.info.title,
          content: this.info.content,
          avatar: this.avatars[this.avatarsChoosed][0].id,
          tips: !this.voteDialog.confirm
            ? undefined
            : [
              {
                type: "vote",
                question: this.voteDialog.question,
                answers: this.voteDialog.answer
                  .slice(0, this.voteDialog.slider)
                  .join("$^^$"),
              },
            ],
        })
          .then((res) => {
            that.showSnackbar(res, 'ok')
            setTimeout(() => {
              that.$router.go(-1)
            }, 500);
          })
          .catch(e => {
            that.showSnackbar(e)
          })
      }
    },
    chooseAvatar() {
      this.avatarChooseDialog = !this.avatarChooseDialog;
      if (this.avatarsChoosed != 10) return;
      let that = this;
      that.$request('get', '/user/getAvatar', { date: new Date().getTime(), num: 9 })
        .then(res => {
          that.avatars = res.data;
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar(e)
        })
    },
  },
  watch: {
    'avatarChooseDialog'(v) {
      this.$store.change_dialog(v, 'avatarChooseDialog')
    },

    '$store.state.dialog'(v) {
      if (this.$store.state.dialogName == 'avatarChooseDialog')
        this.avatarChooseDialog = v
      else if (this.$store.state.dialogName == 'voteDialog.show')
        this.voteDialog.show = v
    },
    'voteDialog.show'(v) {
      this.$store.change_dialog(v, 'voteDialog.show')
    },

  },
};
</script>

<style>
.main_container_edit {
  margin-top: 60px;
  width: 90%;
}
.intro {
  width: 100px;
}
.image {
  right: 0;
  bottom: 0;
}

.lucency {
  opacity: 0.6;
}
</style>



